.container.testimonials__container {
  width: 40%;
  padding-bottom: 4rem;
}

.client__avatar{
  width: 9rem;
  aspect-ratio: 1/1;
  overflow: hidden;
  border-radius: 50%;
  margin: 0 auto 1rem;
  border: 0.4rem solid var(--color-primary-variant);
}

.client__avatar:hover{
  transform: scale(1.01);
  box-shadow:
           inset 0 0 5px #fff,      /* inner white */
           inset 5px 0 30px #f0f,   /* inner left magenta short */
           inset -5px 0 30px #0ff,  /* inner right cyan short */
           inset 1px 0 10px #f0f,  /* inner left magenta broad */
           inset -1px 0 10px #0ff, /* inner right cyan broad */
           0 0 1px #fff,            /* outer white */
          -10px 0 20px #f0f,        /* outer left magenta */
          10px 0 20px #0ff;         /* outer right cyan */   
}

.testimonial{
  background: var(--color-bg-variant);
  text-align: center;
  padding: 2rem;
  border-radius: 2rem;
  user-select: none;
}

.client__review{
  color: var(--color-light);
  font-weight: 300;
  display: block;
  width: 80%;
  margin: 0.8rem auto 0;
}

.swiper-initialized .swiper-horizontal .swiper-pointer-events .swiper-backface-hidden{
  background: var(--color-primary);
}

/*===========Media Queries(Medium Devices)============*/
@media screen and (max-width:1024px) {
  .container.testimonials__container{
    width: 60%;
  }
}
 


/*===========Media Queries(Small Devices)============*/
@media screen and (max-width:600px) {
  .container.testimonials__container{
    width: var(--container-width-sm);
  }

  .client__review{
    width: var(--container-width-sm);
  }

}