.about__container{
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me{
  width:100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,transparent,var(--color-primary),transparent
  );
  display: grid;
  place-items: center;

}

.about__me-image{
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(-10deg);
  transition: var(--transition) ;
}

.about__me-image:hover{
  transform: rotate(0);
  
}

.about__cards{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card{
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  transition: ease-out;
  
}

.about__card:hover{
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: pointer;
  box-shadow:
    inset 0 0 5px #fff,      /* inner white */
    inset 20px 0 80px #f0f,   /* inner left magenta short */
    inset -20px 0 80px #0ff,  /* inner right cyan short */
    inset 20px 0 10px #f0f,  /* inner left magenta broad */
    inset -20px 0 10px #0ff, /* inner right cyan broad */
    0 0 5px #fff,            /* outer white */
    -10px 0 50px #f0f,        /* outer left magenta */
    10px 0 50px #0ff;         /* outer right cyan */

}

.about__icon{
  color: var(--color-primary);
  font-size :1.4rem;
  margin-bottom: 1rem;
  
}

.about__card h5{
  font-size: 0.95rem;
}

.about__card small{
  font-size: 0.7rem;
  color: var(--color-light);
}

.about__content p {
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}

/*===========Media Queries(Medium Devices)============*/
@media screen and (max-width:1024px) {
  .about__container{
    grid-template-columns: 1fr;
    gap: 1rem;    
  }

  .about__me{
    width: 50%;
    margin: 2rem auto 4rem;
    margin-top: 10%;
    margin-bottom: 15%;
  }

  .about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
  }
 
}


/*===========Media Queries(Small Devices)============*/
@media screen and (max-width:600px) {
  .about__me{
    width: 65%;
    margin: 0 auto 3rem;
    margin-top: 10%;
    margin-bottom: 15%;
  }

  .about__cards{
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about__content{
    text-align: center;
  }

  .about__content p{
    margin: 1.5rem 0;
  }
  
}